import React, { Component } from 'react';

export default class About extends Component {

  render() {
    return (

      <section id="about">
        <div className="row">
          <div className="three columns">
            <img className="profile-pic" src="https://cdn.glitch.com/9dc6e0db-b25c-4829-957e-3051ecf08a54%2FChico_main.jpg?v=1603119389086" alt="" />
          </div>
          <div className="nine columns main-col">
            <h2>About Me</h2>
            <p>I'm a full time Computer Science student interested in fullstack web development
               and cybersecurity. I think innovation and security are equally important in a technologically focused society.
            </p>
            <div className="row">
              <div className="columns contact-details">
                <h2>Contact Details</h2>
                <p className="address">
                  <span>Andrew Alagna</span><br />
                  <span>
                    Flushing (Queens), NY 11367 US
                  </span><br />
                <span>(917)-601-9404</span><br />
                  <span>aalagna04@gmail.com</span>
                </p>
              </div>
              <div className="columns download">
                <p>
                  <a href="#resume" className="button"><i className="{/*fa fa-download*/}"/>My resume</a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
};
