import React, { Component } from 'react';
export default class Header extends Component {

  render() {
    let resumeData = this.props.resumeData;
    return (

      <header id="home">
        <nav id="nav-wrap">
          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
          <a className="mobile-btn" href="#Header" title="Hide navigation">Hide navigation</a>
          <ul id="nav" className="nav">
            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
            <li><a className="smoothscroll" href="#about">About</a></li>
            <li><a className="smoothscroll" href="#resume">Resume</a></li>
            {/*<li><a className="smoothscroll" href="#portfolio">Works</a></li>*/}
            <li><a className="smoothscroll" href="#testimonials">Quotes</a></li>
            <li><a className="smoothscroll" href="#contact">Contact</a></li>

          </ul> {/* end #nav */}
        </nav> {/* end #nav-wrap */}
        <div className="row banner">
          <div className="banner-text">
            <h1 className="responsive-headline">I'm {resumeData.name}</h1>
            <h3>A curious software developer who focuses on cyber-security,  {resumeData.roleDescription} <a className="smoothscroll" href="#about">Keep scrolling </a>
              to learn more <a className="smoothscroll" href="#about">about me</a>.</h3>
            <hr />
            <ul className="social">
              <li><a href="https://www.linkedin.com/in/andrew-a-10b88215b/" rel="noreferrer" target="_blank"><i className="fa fa-linkedin" /></a></li>
              <li><a href="https://www.instagram.com/drewal24/" rel="noreferrer" target="_blank"><i className="fa fa-instagram" /></a></li>
              <li><a href="https://www.github.com/aalagna04" rel="noreferrer" target="_blank"><i className="fa fa-github" /></a></li>
            </ul>
          </div>
        </div>
        <p className="scrolldown">
          <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
        </p>
      </header>
    );
  }
};
